import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Swal from 'sweetalert2';
import Web3 from 'web3';

import NavigationBar from "./components/NavigationBar";
import Home from "./components/Home/Home";
import Team from "./components/Team/Team";
import Tokenomics from "./components/Tokenomics/Tokenomics";
import Roadmap from "./components/Roadmap/Roadmap";
import Farm from "./components/Farm/Farm";
import Stake from "./components/Stake/Stake";
import Footer from "./components/Footer";

class App extends Component {

	state = {
		address: `0x000000000000000000000000000000000000`,
		connection: `CONNECT WALLET`,
	}

	componentDidMount(){
		this.connectToWallet = this.connectToWallet.bind(this);
	}

	async componentWillMount(){
		
		const connectToWallet = await this.connectToWallet();
		if(connectToWallet){
				await this.loadBlockchainData();
		}	
	}
	
	connectToWallet() {
		if (window.ethereum) {
			window.web3 = new Web3(window.ethereum);    
			window.ethereum.enable();  
			return true;
		} else {
			Swal.fire(
				'Notice',
				`You must use Web3 compatible browsers like Trust wallet on phone or Metamask on desktop.`,
				'info'
			)
			return false;
		}
	}

	async loadBlockchainData() {
		const web3 = new Web3(Web3.givenProvider || "https://bsc-dataseed.binance.org/");

		const accountsAddress = await web3.eth.getAccounts();

		this.setState({
			address: accountsAddress[0],
			connection: accountsAddress[0].length === 0 ? `CONNECT WALLET` : accountsAddress[0]
		});
	}

	render() {
	return (
			<div>
				<NavigationBar {...this.state} connectToWallet={this.connectToWallet} />
				<Switch>
					{/* <Route exact component={Team} path="/team" /> */}
					<Route exact component={Tokenomics} path="/tokenomics" />
					<Route exact component={Roadmap} path="/roadmap" />
					<Route exact component={Farm} path="/farm" />
					<Route exact component={Stake} path="/stake" />
					<Route exact component={Home} path="/" />
				</Switch>
				<Footer />
			</div>
		);
	}
}

export default App;
