import React, { Component } from "react";

class Team extends Component {
	state = {
		team: [
			{
				name: `Parveen Anand`,
				image: `assets/img/team/1.jpg`,
				position: `Lead Designer`,
				icon: `assets/icons/twitter_svg.svg`,
				twitter: `@x`,
				twitterLink: `https://twitter.com/`,
				facebookLink: `https://twitter.com/`,
				linkedInLink: `https://twitter.com/`,
			},
			{
				name: `Diana Petersen`,
				image: `assets/img/team/2.jpg`,
				position: `Lead Marketer`,
				icon: `assets/icons/twitter_svg.svg`,
				twitter: `@x`,
				twitterLink: `https://twitter.com/`,
				facebookLink: `https://twitter.com/`,
				linkedInLink: `https://twitter.com/`,
			},
			{
				name: `Larry Parker`,
				image: `assets/img/team/3.jpg`,
				position: `Lead Developer`,
				icon: `assets/icons/twitter_svg.svg`,
				twitter: `@x`,
				twitterLink: `https://twitter.com/`,
				facebookLink: `https://twitter.com/`,
				linkedInLink: `https://twitter.com/`,
			},
		]
	}

	render() {
		return (
			<div>
				<section class="page-section" id="team">
					<div class="container">
						<div class="text-center">
							<br/>
							<h2 class="section-heading text-uppercase">Our Amazing Team</h2>
							{/* <h3 class="section-subheading text-muted">Lorem ipsum dolor sit amet consectetur.</h3> */}
							<hr/><br/>
						</div>
						<div class="row">
							{this.state.team.map((row, index) => {
								return (
									<div class="col-lg-4" key={index}>
										<div class="team-member">
											<img class="mx-auto rounded-circle" src={row.image} alt="..." />
											<h4>{row.name}</h4>
											<p class="text-white">{row.position}</p>
											{/* <a class="btn btn-dark btn-social mx-2" href={row.twitterLink} target="_blank"><i class="fab fa-twitter"></i></a>
											<a class="btn btn-dark btn-social mx-2" href={row.facebookLink} target="_blank"><i class="fab fa-facebook-f"></i></a> */}
											<a class="btn btn-dark btn-social mx-2" href={row.linkedInLink} target="_blank"><i class="fab fa-linkedin-in"></i></a>
										</div>
									</div>
								)
							})}
						</div>
						{/* <div class="row">
							<div class="col-lg-8 mx-auto text-center"><p class="large text-muted">Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aut eaque, laboriosam veritatis, quos non quis ad perspiciatis, totam corporis ea, alias ut unde.</p></div>
						</div> */}
					</div>
				</section>
				<br/><br/><br/><br/><br/>
			</div>
		);
	}
}

export default Team;
