import React, { Component } from "react";

import Utility from "../Utility";

class Stake extends Component {
	state = {
		countdown: {
			days: 0,
			hours: 0,
			minutes: 0,
			seconds: 0,
		},
	}

	componentDidMount(){
		let timer;

		let compareDate = new Date(`August 8, 2021`);
		compareDate.setDate(compareDate.getDate()); //just for this demo today + 7 days

		timer = setInterval(() => {
			this.timeBetweenDates(compareDate, timer);
		}, 1000);
	}

	createCountdown(timestamp, day){
		let timer;
  
		const oneDay = 24 * 60 * 60 * 1000;
		
		const diffDays = Math.round(Math.abs((new Date(timestamp * 1000) - new Date(new Date().toLocaleString("en-US"))) / oneDay));
  
		timer = setInterval(() => {
			this.setState({
				countdown: {
					...Utility.timeBetweenDates(Utility.addDays(timestamp, diffDays + day), timer),
				}
			});
		}, 1000);
	}

	timeBetweenDates(toDate, timer) {
		let dateEntered = toDate;
		let now = new Date();
		let difference = dateEntered.getTime() - now.getTime();

		if (difference <= 0) {

			clearInterval(timer);
		
		} else {
			
			let seconds = Math.floor(difference / 1000);
			let minutes = Math.floor(seconds / 60);
			let hours = Math.floor(minutes / 60);
			let days = Math.floor(hours / 24);

			hours %= 24;
			minutes %= 60;
			seconds %= 60;

			this.setState({
				countdown: {
					days,
					hours,
					minutes,
					seconds,
				}
			});
		}
	}

	render() {
		return (
			<div>
				
				<section class="page-section">
					<div class="container">
						<div class="text-center">
							<br/>
							<h2 class="section-heading text-uppercase">Staking</h2>
							<h3 class="section-subheading text-muted">Launches when the timer goes down.</h3>
							<hr/><br/>
						</div>
						<div class="row">
							<div class="col-12">
								<h1 className="text-center"> {this.state.countdown.days}d : {this.state.countdown.hours}h : {this.state.countdown.minutes}m : {this.state.countdown.seconds}s</h1>
							</div>
						</div>
						<br/>
						<hr/>
						<br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
						<br/><br/><br/><br/><br/>
					</div>
				</section>

			</div>
		);
	}
}

export default Stake;
