
module.exports = {
    createCountdown(timestamp, day){
        let timer;

        const oneDay = 24 * 60 * 60 * 1000;

        const diffDays = Math.round(Math.abs((new Date(timestamp * 1000) - new Date(new Date().toLocaleString("en-US"))) / oneDay));

        timer = setInterval(() => {
        return this.timeBetweenDates(this.addDays(timestamp, diffDays + day), timer);
        }, 1000);
    },
    addDays(timestamp, days) {
        var date = new Date(timestamp * 1000);
        date.setDate(date.getDate() + days);
        return date;
    },
    timeBetweenDates(toDate, timer) {
        let dateEntered = toDate;
        let now = new Date(new Date().toLocaleString("en-US"));
        let difference = dateEntered.getTime() - now.getTime();
    
        
        if (difference <= 0) {
            clearInterval(timer);
        } else {
            
            let seconds = Math.floor(difference / 1000);
            let minutes = Math.floor(seconds / 60);
            let hours = Math.floor(minutes / 60);
            let days = Math.floor(hours / 24);
    
            hours %= 24;
            minutes %= 60;
            seconds %= 60;
            
            return {
                days,
                hours: `${`${hours}`.length === 1 ? `0${hours}` : hours}`,
                minutes: `${`${minutes}`.length === 1 ? `0${minutes}` : minutes}`,
                seconds: `${`${seconds}`.length === 1 ? `0${seconds}` : seconds}`,
            };

        }
    },
    trimString(string){
        return `${string.substr(0, 5)}....${string.substr(string.length - 5)}`;
    },
    divideBy(amount) {
        return amount / 1000000000000000000;
    },
}