import React, { Component } from "react";
import { HashLink as Link } from "react-router-hash-link";

import Utility from "./Utility";
import { BABY_YEARN_ADDRESS, BABY_YEARN_ABI } from "./Config";

class TopNavigationBar extends Component {

	render() {
		return (
			<nav className="navbar navbar-expand-lg navbar-dark fixed-top" id="mainNav">
				<div className="container">
					<a className="navbar-brand" href="#page-top">{/*<img src="assets/img/navbar-logo.svg" alt="..." /> */}</a>
					<button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarResponsive" aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
						Menu
						<i className="fas fa-bars ms-1"></i>
					</button>
					<div className="collapse navbar-collapse" id="navbarResponsive">
						<ul className="navbar-nav text-uppercase ms-auto py-4 py-lg-0">
							<li className="nav-item"><Link to={{ pathname: "/" }} className="nav-link" href="#home">Home</Link></li>
							{/* <li className="nav-item"><a className="nav-link" href={`https://exchange.pancakeswap.finance/#/swap?outputCurrency=${BABY_YEARN_ABI}`} target="_blank">Buy</a></li>
							<li className="nav-item"><Link to={{ pathname: "/team" }} className="nav-link" href="#team">Team</Link></li> */}
							<li className="nav-item"><Link to={{ pathname: "/tokenomics" }} className="nav-link" href="#tokenomics">Tokenomics</Link></li>
							<li className="nav-item"><Link to={{ pathname: "/roadmap" }} className="nav-link" href="#roadmap">Roadmap</Link></li>
							<li className="nav-item"><Link to={{ pathname: "/stake" }} className="nav-link" href="#stake">Stake</Link></li>
							<li className="nav-item"><Link to={{ pathname: "/farm" }} className="nav-link" href="#farm">Farm</Link></li>
							{ this.props.connection == "CONNECT WALLET" ? (
								<li className="nav-item"><a href="#" className="nav-link" href="#connect" onClick={() => {this.props.connectToWallet()}}>{this.props.connection}</a></li>
							) : (
								<li className="nav-item"><a href="#" className="nav-link" href={`https://bscscan.com/address/${this.props.connection}`} target="_blank" style={{ color: '#ffc800' }}>{Utility.trimString(this.props.connection)}</a></li>
							)
							}
							
						</ul>
					</div>
				</div>
			</nav>
		);
	}
}
export default TopNavigationBar;
