import React, { Component } from "react";

class Tokenomics extends Component {
    state = {
		tokenomics: [
			{
				icon: `assets/tokenomics/total supply.png`,
				title: `Total Supply`,
				description: `10,000,000,000,000`,
			},
            {
				icon: `assets/tokenomics/initial burned.png`,
				title: `Initially Burned (50%)`,
				description: `5,000,000,000,000`,
			},
            {
				icon: `assets/tokenomics/auto liquidity.png`,
				title: `Auto Liquidity`,
				description: `5% to liquidity & locked.`,
			},
            {
				icon: `assets/tokenomics/auto burned.png`,
				title: `Auto Burn`,
				description: `5% sent directly to burn address.`,
			},
		]
	}

	render() {
		return (
			<section class="page-section" id="services">
                <div class="container">
                    <div class="text-center">
                        <br/>
                        <h2 class="section-heading text-uppercase">Tokenomics</h2>
                        <hr/><br/>
                        {/* <h3 class="section-subheading">Lorem ipsum dolor sit amet consectetur.</h3> */}
                    </div>
                    <div class="row text-center">
                        {this.state.tokenomics.map((row, index) => {
                            return (
                                <div class="col-md-3" key={index}>
                                    <span class="fa-stack fa-4x">
                                        {/* <i class="fas fa-circle fa-stack-2x text-primary"></i>
                                        <i class="fas fa-dollar-sign fa-stack-1x fa-inverse"></i> */}
                                        <img src={row.icon} className="img-fluid" height="150" width="150" />
                                    </span>
                                    <br/><br/>
                                    <h4 class="my-3">{row.title}</h4>
                                    <p class="text-white">{row.description}</p>
                                </div>
                            )
                        })}
                    </div>
                    <br/><br/><br/><br/>
			        <br/><br/><br/><br/><br/>
                </div>
            </section>
		);
	}
}

export default Tokenomics;
